<template>
  <div class="chat-container">
    <!--  -->
    <div class="session-list-panel">
      <div class="panel-header">
        <h3></h3>
        <el-button type="primary" size="small" @click="createNewSession">
          <el-icon><icon-plus /></el-icon>
          <span></span>
        </el-button>
      </div>
      
      <div class="search-box">
        <el-input 
          v-model="searchKeyword" 
          placeholder="" 
          clearable 
        >
          <template #prefix>
            <el-icon><icon-search /></el-icon>
          </template>
        </el-input>
      </div>
      
      <div class="session-list">
        <div 
          v-for="session in filteredSessions" 
          :key="session.id" 
          class="session-item"
          :class="{ active: session.id === currentSession.id }"
          @click="switchSession(session)"
        >
          <div class="session-icon">
            <el-icon><icon-chat-dot-round /></el-icon>
          </div>
          <div class="session-info">
            <div class="session-title">{{ session.title }}</div>
            <div class="session-meta">
              <span class="session-time">{{ formatDate(session.updatedAt) }}</span>
              <span class="session-count">{{ session.messageCount }}</span>
            </div>
          </div>
          <div class="session-actions">
            <el-dropdown trigger="click" @command="handleSessionAction($event, session)">
              <el-icon><icon-more /></el-icon>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item command="rename"></el-dropdown-item>
                  <el-dropdown-item command="export"></el-dropdown-item>
                  <el-dropdown-item command="delete" divided></el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
          </div>
        </div>
      </div>
    </div>
    
    <!--  -->
    <div class="chat-panel">
      <!--  -->
      <div class="chat-header">
        <div class="session-title">{{ currentSession.title }}</div>
        <div class="toolbar">
          <el-tooltip content="">
            <el-button circle @click="showDataSourcePanel = true">
              <el-icon><icon-data-line /></el-icon>
            </el-button>
          </el-tooltip>
          <el-tooltip content="">
            <el-button circle @click="showModelPanel = true">
              <el-icon><icon-monitor /></el-icon>
            </el-button>
          </el-tooltip>
          <el-tooltip content="">
            <el-button circle @click="clearMessages">
              <el-icon><icon-delete /></el-icon>
            </el-button>
          </el-tooltip>
        </div>
      </div>
      
      <!--  -->
      <div class="message-container" ref="messageContainer">
        <template v-if="currentSession.messages.length === 0">
          <div class="empty-state">
            <div class="empty-icon">
              <el-icon><icon-chat-square /></el-icon>
            </div>
            <div class="empty-text"></div>
          </div>
        </template>
        
        <template v-else>
          <div 
            v-for="(message, index) in currentSession.messages" 
            :key="index"
            class="message-wrapper"
            :class="{'user-message': message.role === 'user', 'ai-message': message.role === 'assistant'}"
          >
            <div class="message-avatar">
              <el-avatar>
                <el-icon>
                  <icon-user v-if="message.role === 'user'" />
                  <icon-cpu v-else />
                </el-icon>
              </el-avatar>
            </div>
            <div class="message-content">
              <div class="message-header">
                <span class="message-sender">{{ message.role === 'user' ? '' : '' }}</span>
                <span class="message-time">{{ formatTime(message.timestamp) }}</span>
              </div>
              <div class="message-body" v-html="formatMessage(message.content)"></div>
              <div class="message-actions" v-if="message.role === 'assistant'">
                <el-button text type="primary" size="small" @click="copyMessage(message)">
                  <el-icon><icon-document-copy /></el-icon>
                  <span></span>
                </el-button>
                <el-button text type="primary" size="small" @click="rateMessage(message, 'like')">
                  <el-icon><icon-thumb-up /></el-icon>
                  <span></span>
                </el-button>
                <el-button text type="danger" size="small" @click="rateMessage(message, 'dislike')">
                  <el-icon><icon-thumb-down /></el-icon>
                  <span></span>
                </el-button>
              </div>
            </div>
          </div>
          
          <div class="typing-indicator" v-if="isTyping">
            <span class="dot"></span>
            <span class="dot"></span>
            <span class="dot"></span>
          </div>
        </template>
      </div>
      
      <!--  -->
      <div class="input-container">
        <div class="toolbar">
          <el-tooltip content="">
            <el-button circle @click="uploadFile">
              <el-icon><icon-upload /></el-icon>
            </el-button>
          </el-tooltip>
          <el-tooltip content="">
            <el-button circle @click="toggleVoiceInput">
              <el-icon><icon-microphone /></el-icon>
            </el-button>
          </el-tooltip>
        </div>
        
        <div class="input-area">
          <el-input
            v-model="messageInput"
            type="textarea"
            :rows="4"
            placeholder="..."
            resize="none"
            @keydown.enter.ctrl.prevent="sendMessage"
          />
          
          <el-button 
            type="primary" 
            :disabled="!messageInput.trim()" 
            @click="sendMessage"
          >
            
            <el-icon><icon-position /></el-icon>
          </el-button>
        </div>
        
        <div class="input-tips">
           Ctrl + Enter 
        </div>
      </div>
    </div>
    
    <!--  -->
    <el-drawer
      v-model="showDataSourcePanel"
      title=""
      direction="rtl"
      size="30%"
    >
      <div class="datasource-selector">
        <el-checkbox-group v-model="selectedDataSources">
          <div v-for="source in availableDataSources" :key="source.id" class="datasource-item">
            <el-checkbox :value="source.id">
              <div class="datasource-info">
                <div class="datasource-name">{{ source.name }}</div>
                <div class="datasource-type">{{ source.type }}</div>
              </div>
            </el-checkbox>
          </div>
        </el-checkbox-group>
      </div>
      
      <template #footer>
        <div class="drawer-footer">
          <el-button @click="showDataSourcePanel = false"></el-button>
          <el-button type="primary" @click="confirmDataSources"></el-button>
        </div>
      </template>
    </el-drawer>
    
    <!--  -->
    <el-drawer
      v-model="showModelPanel"
      title=""
      direction="rtl"
      size="30%"
    >
      <div class="model-selector">
        <el-radio-group v-model="selectedModel">
          <div v-for="model in availableModels" :key="model.id" class="model-item">
            <el-radio :value="model.id">
              <div class="model-info">
                <div class="model-name">{{ model.name }}</div>
                <div class="model-type">{{ model.type }}</div>
              </div>
            </el-radio>
          </div>
        </el-radio-group>
      </div>
      
      <template #footer>
        <div class="drawer-footer">
          <el-button @click="showModelPanel = false"></el-button>
          <el-button type="primary" @click="confirmModel"></el-button>
        </div>
      </template>
    </el-drawer>
    
    <!--  -->
    <el-dialog v-model="renameDialogVisible" title="" width="30%">
      <el-input v-model="newSessionTitle" placeholder=""></el-input>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="renameDialogVisible = false"></el-button>
          <el-button type="primary" @click="confirmRename"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref, reactive, computed, onMounted, nextTick, watch } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage, ElMessageBox } from 'element-plus'
import * as marked from 'marked'
import DOMPurify from 'dompurify'
import { v4 as uuidv4 } from 'uuid'
// Element Plus
import { 
  Plus, Search, ChatDotRound, More, DataLine, Monitor, Delete, 
  ChatSquare, User, Cpu, DocumentCopy, ThumbUp, ThumbDown, 
  Upload, Microphone, Position 
} from '@element-plus/icons-vue'

export default {
  name: 'ChatView',
  
  // 
  components: {
    'icon-plus': Plus,
    'icon-search': Search,
    'icon-chat-dot-round': ChatDotRound,
    'icon-more': More,
    'icon-data-line': DataLine,
    'icon-monitor': Monitor,
    'icon-delete': Delete,
    'icon-chat-square': ChatSquare,
    'icon-user': User,
    'icon-cpu': Cpu,
    'icon-document-copy': DocumentCopy,
    'icon-thumb-up': ThumbUp,
    'icon-thumb-down': ThumbDown,
    'icon-upload': Upload,
    'icon-microphone': Microphone,
    'icon-position': Position
  },
  
  setup() {
    // 
    const router = useRouter()
    
    // 
    const searchKeyword = ref('')
    
    // 
    const renameDialogVisible = ref(false)
    const newSessionTitle = ref('')
    const sessionToRename = ref(null)
    
    // 
    const messageInput = ref('')
    const isTyping = ref(false)
    const messageContainer = ref(null)
    
    // 
    const showDataSourcePanel = ref(false)
    const availableDataSources = ref([
      { id: 1, name: 'MySQL', type: 'MySQL' },
      { id: 2, name: 'PostgreSQL', type: 'PostgreSQL' },
      { id: 3, name: 'SQL Server', type: 'SQLServer' },
      { id: 4, name: 'Oracle', type: 'Oracle' }
    ])
    const selectedDataSources = ref([1]) // 
    
    // 
    const showModelPanel = ref(false)
    const availableModels = ref([
      { id: 1, name: 'GPT-4', type: '' },
      { id: 2, name: 'Claude 3', type: '' },
      { id: 3, name: 'LLaMA 3', type: '' },
      { id: 4, name: 'FinBERT', type: '' }
    ])
    const selectedModel = ref(1) // 
    
    // 
    const sessions = ref([
      {
        id: 1,
        title: '',
        createdAt: '2023-11-10T08:30:00Z',
        updatedAt: '2023-11-10T08:45:00Z',
        messageCount: 5,
        messages: [
          {
            id: 101,
            role: 'user',
            content: '',
            timestamp: '2023-11-10T08:30:00Z'
          },
          {
            id: 102,
            role: 'assistant',
            content: '',
            timestamp: '2023-11-10T08:30:10Z'
          },
          {
            id: 103,
            role: 'user',
            content: '202310',
            timestamp: '2023-11-10T08:31:00Z'
          },
          {
            id: 104,
            role: 'assistant',
            content: '202310\n\n1,258,750\n14.5%\n\n\n- 528,300 (42%)\n- 356,200 (28%)\n- 245,600 (19%)\n- 128,650 (11%)\n\n',
            timestamp: '2023-11-10T08:31:15Z'
          },
          {
            id: 105,
            role: 'user',
            content: '',
            timestamp: '2023-11-10T08:44:00Z'
          }
        ],
        dataSourceIds: [1],
        modelId: 1
      },
      {
        id: 2,
        title: '',
        createdAt: '2023-11-09T10:15:00Z',
        updatedAt: '2023-11-09T10:30:00Z',
        messageCount: 3,
        messages: [],
        dataSourceIds: [1, 2],
        modelId: 1
      },
      {
        id: 3,
        title: '',
        createdAt: '2023-11-08T14:20:00Z',
        updatedAt: '2023-11-08T15:45:00Z',
        messageCount: 8,
        messages: [],
        dataSourceIds: [4],
        modelId: 4
      }
    ])
    
    // 
    const currentSession = ref({...sessions.value[0]})
    
    // 
    const filteredSessions = computed(() => {
      if (!searchKeyword.value) return sessions.value
      
      const keyword = searchKeyword.value.toLowerCase()
      return sessions.value.filter(session => 
        session.title.toLowerCase().includes(keyword)
      )
    })
    
    // 
    const createNewSession = () => {
      const newSession = {
        id: uuidv4(),
        title: ` ${sessions.value.length + 1}`,
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
        messageCount: 0,
        messages: [],
        dataSourceIds: [selectedDataSources.value[0]],
        modelId: selectedModel.value
      }
      
      sessions.value.unshift(newSession)
      currentSession.value = {...newSession}
      
      // 
      messageInput.value = ''
    }
    
    // 
    const switchSession = (session) => {
      // 
      if (session.id === currentSession.value.id) return
      
      // 
      selectedDataSources.value = session.dataSourceIds || [1]
      selectedModel.value = session.modelId || 1
      
      // 
      currentSession.value = {...session}
      
      // 
      if (!session.messages || session.messages.length === 0) {
        // 
        loadSessionMessages(session.id)
      }
      
      // 
      nextTick(() => {
        scrollToBottom()
      })
    }
    
    // 
    const loadSessionMessages = (sessionId) => {
      // API
      // 
      const sessionIndex = sessions.value.findIndex(s => s.id === sessionId)
      if (sessionIndex === -1) return
      
      // 
      setTimeout(() => {
        if (sessionId === 2) {
          // 
          sessions.value[sessionIndex].messages = [
            {
              id: 201,
              role: 'user',
              content: '',
              timestamp: '2023-11-09T10:15:00Z'
            },
            {
              id: 202,
              role: 'assistant',
              content: '(users)\n\n|  |  |  |\n| --- | --- | --- |\n| id | INT |  |\n| username | VARCHAR(50) |  |\n| email | VARCHAR(100) |  |\n| password_hash | VARCHAR(128) |  |\n| created_at | TIMESTAMP |  |\n| status | TINYINT | 1-0- |',
              timestamp: '2023-11-09T10:15:10Z'
            },
            {
              id: 203,
              role: 'user',
              content: '',
              timestamp: '2023-11-09T10:20:00Z'
            }
          ]
          currentSession.value = {...sessions.value[sessionIndex]}
        } else if (sessionId === 3) {
          // 
          sessions.value[sessionIndex].messages = [
            {
              id: 301,
              role: 'user',
              content: '2023',
              timestamp: '2023-11-08T14:20:00Z'
            },
            {
              id: 302,
              role: 'assistant',
              content: '2023\n\n****8,750,000\n****6,120,000\n****2,630,000\n****30.1%\n\n\n- 18.5%\n- 12.3%\n- 35.8%\n- 3.9\n\n\n1. 65%\n2. 25%\n3. 10%',
              timestamp: '2023-11-08T14:20:30Z'
            },
            {
              id: 303,
              role: 'user',
              content: '',
              timestamp: '2023-11-08T14:45:00Z'
            }
          ]
          currentSession.value = {...sessions.value[sessionIndex]}
        }
      }, 500)
    }
    
    // 
    const sendMessage = async () => {
      if (!messageInput.value.trim()) return
      
      const userMessage = {
        id: uuidv4(),
        role: 'user',
        content: messageInput.value,
        timestamp: new Date().toISOString()
      }
      
      // 
      currentSession.value.messages.push(userMessage)
      
      // 
      const sessionIndex = sessions.value.findIndex(s => s.id === currentSession.value.id)
      if (sessionIndex !== -1) {
        sessions.value[sessionIndex].messages = [...currentSession.value.messages]
        sessions.value[sessionIndex].messageCount = currentSession.value.messages.length
        sessions.value[sessionIndex].updatedAt = new Date().toISOString()
      }
      
      // 
      messageInput.value = ''
      
      // 
      nextTick(() => {
        scrollToBottom()
      })
      
      // 
      isTyping.value = true
      
      try {
        // AI
        await new Promise(resolve => setTimeout(resolve, 1500))
        
        // AI
        const aiMessage = {
          id: uuidv4(),
          role: 'assistant',
          content: await generateAIResponse(userMessage.content),
          timestamp: new Date().toISOString()
        }
        
        // AI
        currentSession.value.messages.push(aiMessage)
        
        // 
        if (sessionIndex !== -1) {
          sessions.value[sessionIndex].messages = [...currentSession.value.messages]
          sessions.value[sessionIndex].messageCount = currentSession.value.messages.length
          sessions.value[sessionIndex].updatedAt = new Date().toISOString()
        }
        
        // 
        isTyping.value = false
        
        // 
        nextTick(() => {
          scrollToBottom()
        })
      } catch (error) {
        console.error('AI', error)
        isTyping.value = false
        
        // 
        ElMessage.error('')
      }
    }
    
    // AI
    const generateAIResponse = async (userInput) => {
      // APIAI
      // 
      
      const input = userInput.toLowerCase()
      
      if (input.includes('') || input.includes('') || input.includes('')) {
        return '\n\n```sql\nSELECT * FROM users WHERE status = 1;\n```\n\n1,20587.3%17512.7%\n\n'
      }
      
      if (input.includes('') || input.includes('') || input.includes('')) {
        return '2023\n\n- 3,456,00015.2%\n- 4,127,00018.7%\n- 4,890,00021.3%\n- 5,200,00023.5%\n\n'
      }
      
      if (input.includes('') || input.includes('') || input.includes('')) {
        return '\n\n```chart\n{\n  "type": "bar",\n  "data": {\n    "labels": ["Q1", "Q2", "Q3", "Q4"],\n    "datasets": [{\n      "label": "2023()",\n      "data": [345.6, 412.7, 489.0, 520.0]\n    }]\n  }\n}\n```\n\n'
      }
      
      return '"' + userInput + '"\n\n1. \n2. \n3. \n\n'
    }
    
    // 
    const copyMessage = (message) => {
      navigator.clipboard.writeText(message.content)
        .then(() => {
          ElMessage({
            message: '',
            type: 'success'
          })
        })
        .catch(() => {
          ElMessage.error('')
        })
    }
    
    // 
    const rateMessage = (message, rating) => {
      // API
      ElMessage({
        message: rating === 'like' ? '' : '',
        type: rating === 'like' ? 'success' : 'warning'
      })
    }
    
    // 
    const clearMessages = () => {
      ElMessageBox.confirm(
        '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      )
        .then(() => {
          // 
          currentSession.value.messages = []
          
          // 
          const sessionIndex = sessions.value.findIndex(s => s.id === currentSession.value.id)
          if (sessionIndex !== -1) {
            sessions.value[sessionIndex].messages = []
            sessions.value[sessionIndex].messageCount = 0
            sessions.value[sessionIndex].updatedAt = new Date().toISOString()
          }
          
          ElMessage({
            type: 'success',
            message: ''
          })
        })
        .catch(() => {
          // 
        })
    }
    
    // 
    const handleSessionAction = (command, session) => {
      switch (command) {
        case 'rename':
          sessionToRename.value = session
          newSessionTitle.value = session.title
          renameDialogVisible.value = true
          break
        case 'export':
          exportSession(session)
          break
        case 'delete':
          deleteSession(session)
          break
      }
    }
    
    // 
    const confirmRename = () => {
      if (!newSessionTitle.value.trim()) {
        ElMessage.warning('')
        return
      }
      
      const sessionIndex = sessions.value.findIndex(s => s.id === sessionToRename.value.id)
      if (sessionIndex !== -1) {
        sessions.value[sessionIndex].title = newSessionTitle.value
        
        // 
        if (currentSession.value.id === sessionToRename.value.id) {
          currentSession.value.title = newSessionTitle.value
        }
        
        ElMessage({
          message: '',
          type: 'success'
        })
      }
      
      renameDialogVisible.value = false
    }
    
    // 
    const exportSession = (session) => {
      // 
      const exportData = {
        title: session.title,
        createdAt: session.createdAt,
        updatedAt: session.updatedAt,
        messages: session.messages
      }
      
      // JSON
      const dataStr = JSON.stringify(exportData, null, 2)
      
      // 
      const dataUri = 'data:application/json;charset=utf-8,' + encodeURIComponent(dataStr)
      const exportFileName = `chat-${session.title}-${new Date().toISOString().split('T')[0]}.json`
      
      // 
      const linkElement = document.createElement('a')
      linkElement.setAttribute('href', dataUri)
      linkElement.setAttribute('download', exportFileName)
      linkElement.click()
      
      ElMessage({
        message: '',
        type: 'success'
      })
    }
    
    // 
    const deleteSession = (session) => {
      ElMessageBox.confirm(
        `"${session.title}"`,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      )
        .then(() => {
          // 
          sessions.value = sessions.value.filter(s => s.id !== session.id)
          
          // 
          if (currentSession.value.id === session.id) {
            if (sessions.value.length > 0) {
              currentSession.value = {...sessions.value[0]}
            } else {
              // 
              createNewSession()
            }
          }
          
          ElMessage({
            type: 'success',
            message: ''
          })
        })
        .catch(() => {
          // 
        })
    }
    
    // 
    const confirmDataSources = () => {
      // 
      currentSession.value.dataSourceIds = [...selectedDataSources.value]
      
      // 
      const sessionIndex = sessions.value.findIndex(s => s.id === currentSession.value.id)
      if (sessionIndex !== -1) {
        sessions.value[sessionIndex].dataSourceIds = [...selectedDataSources.value]
      }
      
      showDataSourcePanel.value = false
      
      ElMessage({
        message: '',
        type: 'success'
      })
    }
    
    // 
    const confirmModel = () => {
      // 
      currentSession.value.modelId = selectedModel.value
      
      // 
      const sessionIndex = sessions.value.findIndex(s => s.id === currentSession.value.id)
      if (sessionIndex !== -1) {
        sessions.value[sessionIndex].modelId = selectedModel.value
      }
      
      showModelPanel.value = false
      
      ElMessage({
        message: '',
        type: 'success'
      })
    }
    
    // 
    const uploadFile = () => {
      // 
      ElMessage({
        message: '',
        type: 'info'
      })
    }
    
    // 
    const toggleVoiceInput = () => {
      // 
      ElMessage({
        message: '',
        type: 'info'
      })
    }
    
    // 
    const formatDate = (dateString) => {
      if (!dateString) return ''
      
      const date = new Date(dateString)
      const now = new Date()
      
      // 
      if (date.toDateString() === now.toDateString()) {
        return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
      }
      
      // 
      const yesterday = new Date(now)
      yesterday.setDate(now.getDate() - 1)
      if (date.toDateString() === yesterday.toDateString()) {
        return ' ' + date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
      }
      
      // 
      if (date.getFullYear() === now.getFullYear()) {
        return date.toLocaleDateString([], { month: '2-digit', day: '2-digit' })
      }
      
      // 
      return date.toLocaleDateString()
    }
    
    // 
    const formatTime = (dateString) => {
      if (!dateString) return ''
      
      const date = new Date(dateString)
      return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
    }
    
    // Markdown
    const formatMessage = (content) => {
      if (!content) return ''
      
      // markedMarkdown
      const html = marked.parse(content)
      
      // DOMPurifyHTMLXSS
      return DOMPurify.sanitize(html)
    }
    
    // 
    const scrollToBottom = () => {
      if (messageContainer.value) {
        messageContainer.value.scrollTop = messageContainer.value.scrollHeight
      }
    }
    
    // 
    watch(
      () => currentSession.value.messages,
      () => {
        nextTick(() => {
          scrollToBottom()
        })
      },
      { deep: true }
    )
    
    // 
    onMounted(() => {
      nextTick(() => {
        scrollToBottom()
      })
    })
    
    return {
      // 
      searchKeyword,
      messageInput,
      isTyping,
      renameDialogVisible,
      newSessionTitle,
      showDataSourcePanel,
      showModelPanel,
      messageContainer,
      
      // 
      sessions,
      filteredSessions,
      currentSession,
      availableDataSources,
      selectedDataSources,
      availableModels,
      selectedModel,
      
      // 
      createNewSession,
      switchSession,
      sendMessage,
      copyMessage,
      rateMessage,
      clearMessages,
      handleSessionAction,
      confirmRename,
      confirmDataSources,
      confirmModel,
      uploadFile,
      toggleVoiceInput,
      
      // 
      formatDate,
      formatTime,
      formatMessage,
      scrollToBottom
    }
  }
}
</script> 